@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;600;700&display=swap);
/* Import a modern system font (Inter) for a cleaner, more professional look */

:root {
    --bg-color: #0f1724; /* deep navy */
    --bg-accent: rgba(10, 25, 47, 0.55); /* overlay */
    --text-color: #e6eef9; /* soft off-white */
    --muted: #9fb2d6;
    --accent: #4fb6ff; /* professional blue accent */
    --max-width: 980px;
}

html, body, #root {
    height: 100%;
}

body {
    margin: 0;
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
    background-color: #0f1724;
    background-color: var(--bg-color);
    /* Keep the existing image if present, but add a dark overlay for readability */
    background-image: linear-gradient(rgba(10, 25, 47, 0.55), rgba(10, 25, 47, 0.55)), url(/profile-and-blogs/static/media/dark_earth.3446cd0b.jpg);
    background-image: linear-gradient(var(--bg-accent), var(--bg-accent)), url(/profile-and-blogs/static/media/dark_earth.3446cd0b.jpg);
    background-blend-mode: overlay;
    background-repeat: no-repeat;
    background-position: center center;
    background-attachment: fixed;
    background-size: cover;
    color: #e6eef9;
    color: var(--text-color);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    line-height: 1.6;
    -webkit-text-size-adjust: 100%;
}

/* Utility container to center content and limit line length */
.container {
    /* Make containers fluid by default so content spans the device width
         while keeping inner padding for comfortable reading. Use
         .container--centered to constrain content when needed. */
    width: 100%;
    max-width: 100%;
    margin: 0;
    padding: 24px;
    box-sizing: border-box;
}

.container--centered {
    max-width: 980px;
    max-width: var(--max-width);
    margin: 0 auto;
}

a { color: #4fb6ff; color: var(--accent); text-decoration: none; }
a:hover { text-decoration: underline; }
.App {
  text-align: left;
  color: var(--text-color);
}

.App-header {
  /* keep header full height but transparent so background shows through */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding-top: 48px;
}

.brand {
  font-weight: 700;
  font-size: 1.15rem;
  letter-spacing: 0.4px;
  color: var(--text-color);
}

.App-link {
  color: var(--accent);
}

.content {
  position: relative;
  /* make content span full width of the device while keeping inner padding */
  width: 100%;
  max-width: 100%;
  margin: 75px 0;
  padding: 0 24px;
  box-sizing: border-box;
  display: block;
  font-size: 18px;
  font-weight: 400;
}

/* If you want the textual area constrained, wrap the inner content with
   `.content-inner` and it will be centered with a comfortable max width. */
.content-inner {
  max-width: var(--max-width);
  margin: 0 auto;
}

h1, h2, h3 {
  color: var(--text-color);
  margin: 0 0 12px 0;
  line-height: 1.15;
}

h1 { font-size: 2rem; font-weight: 700; }
h2 { font-size: 1.5rem; font-weight: 600; }

.lead {
  color: var(--muted);
  font-size: 1.05rem;
  margin-bottom: 18px;
}

.card {
  background: rgba(255,255,255,0.03);
  border: 1px solid rgba(255,255,255,0.04);
  border-radius: 10px;
  padding: 18px;
  margin-bottom: 16px;
}

/* Responsive adjustments */
@media (max-width: 640px) {
  .content { padding: 0 16px; font-size: 16px; }
  h1 { font-size: 1.5rem; }
}
.navwarapper {
    display: grid;
    grid-template-columns: 9fr 1fr 1fr 1fr 1fr 3fr;
    text-align: center;  
    margin-bottom: 50px;
    padding: 10px 0px;
    position: fixed;
    width: 100%;
    z-index: 100;
    top: 0px;
    left: 0px;
}

.navlink {
    margin:1px;
    margin-bottom: 5px;
    padding: 6px;
    font-size: 15px;
    color: rgb(139, 243, 163);
    text-decoration: none;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    text-shadow: 2px 1px #b8f5d733;
    align-content: center;
    border: 0px solid #252b28e3;
    border-radius: 5px;
}
.navlink:hover{
    background-color: #00ffb3;
    color:#3a233ab0;
    font-weight: bold;
    transition-duration: 20ms;
    box-shadow: 5px 10px 20px cadetblue inset;
}
